.App {
  text-align: center;
}
.app {
  background: linear-gradient(to bottom, #EFEFEF 50%, #1A2956 50%);
}

.headcolor {
  color:#1A2956;
}

hr {
  margin-top: 1rem;
  margin-bottom: 2rem;
  border: 0;
  border-top: 2px solid rgba(0, 0, 0, 0.1);
}

.d-none {
  display: none;
}

.d-block {
  display: block;
}

.successcolor {
  color: #07bc0c;
}

.loading {
  position: fixed;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  text-align: center;
  background-color: white;
  z-index: 99;
}

.loading-image {
  position: absolute;
  /*background-color: rgba(255,255,255,0.8);*/
  top: 45%;
  left: 48%;
  z-index: 100;
}

.justify-b {
  display: flex;
  justify-content: space-between;
}

.justify-b div {
  width: 20%;
}

@media only screen and (max-width: 1000px) {
  .justify-b {
    display: block;
  }
  .justify-b div {
    /*margin: auto;*/
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    width: 50% !important;
  }
}

@media only screen and (max-width: 600px) {
  .justify-b {
    display: block;
  }
  .justify-b div {
    width: 92%;
  }
}